<template>
  <section id="alarm-list-page" class="df">
    <div class="left-tree tree">
      <tree :nodes="nodes" :setting="treeSetting" ref="tree" />
    </div>
    <div class="right-content">
      <el-tabs v-model="curView" @tab-click="">
        <el-tab-pane label="活动告警" name="1">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1300px;">
                <el-form-item :label="'告警名称'" prop="name">
                  <el-input size="mini" v-model="activeAlarmQuery.name"></el-input>
                </el-form-item>
                
                <el-form-item :label="'告警等级'" prop="targetLevelName">
                  <el-select size="mini" v-model="activeAlarmQuery.targetLevelName">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in alarmLevelList" :key="index" :label="item.levelName" :value="item.levelName">{{ item.levelName }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'告警状态'" prop="state">
                  <el-select size="mini" v-model="activeAlarmQuery.state">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in ['待处理', '已消除']" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'确认状态'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'设备类型'" prop="deviceType">
                  <el-select size="mini" v-model="activeAlarmQuery.deviceType" @input="$set(activeAlarmQuery, 'deviceModel', undefined)">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in deviceTypeList" :key="index" :label="item.type" :value="item.type">{{ item.type }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备型号'" prop="deviceModel">
                  <el-select size="mini" v-model="activeAlarmQuery.deviceModel">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in (deviceTypeList.find(v => v.type === activeAlarmQuery.deviceType) || {models: []}).models" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备名称'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <br/>
                
                <el-form-item :label="'告警发生时间'" prop="startBeginTime">
                  <el-date-picker
                    v-model="activeAlarmQuery.startBeginTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop=" startEndTime">
                  <el-date-picker
                    v-model="activeAlarmQuery.startEndTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-button type="primary" size="mini" @click="getActiveAlarmList()" style="margin-left: 33px;">确认</el-button>
                <el-button type="primary" size="mini" @click="activeAlarmQuery = {}">清除</el-button>
              </el-form>
            </div>

            <div class="list df dfv">
              <div class="list-header">
                <h3>活动告警列表</h3>
                <div class="btn-group">
                  <el-button type='primary' size="mini" @click="additionalInfoToModal()">告警拓展信息</el-button>
                  <el-button type='primary' size="mini" @click="alarmConfirmToModal()">告警确认</el-button>
                  <custom-column v-model="columnList" 
                    :name="'alarmListCustomColumn'" 
                    :enterprise-code="curEnterprise.code" 
                    :server-code="curServer.code"
                    :refreshCallback="getAdditionalList">自定义列</custom-column>
                </div>
              </div>
              <div class="table-box df dfv">
                <custom-table ref="activeAlarmList" :data="activeAlarmList" :columns="columnListForTable" height="100%"></custom-table>

                <!-- Pagination -->
                <el-col :span="24" class="toolbar">
                  <el-pagination
                    layout="total, prev, pager, next,sizes, jumper"
                    @current-change="activePagi.handleCurrentChange"
                    @size-change="activePagi.pageSizeChange"
                    :page-sizes="[10, 15, 50, 100]"
                    :page-size="activePagi.pageSize"
                    :total="activePagi.total"
                    style="float:right;">
                  </el-pagination>
                </el-col>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="历史告警" name="second">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1350px;">
                <el-form-item :label="'告警名称'" prop="name">
                  <el-input size="mini" v-model="historyAlarmQuery.name"></el-input>
                </el-form-item>
                
                <el-form-item :label="'告警等级'" prop="targetLevelName">
                  <el-select size="mini" v-model="historyAlarmQuery.targetLevelName">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in alarmLevelList" :key="index" :label="item.levelName" :value="item.levelName">{{ item.levelName }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'告警状态'" prop="state">
                  <el-select size="mini" v-model="historyAlarmQuery.state">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in ['待处理', '已消除']" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'确认状态'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'设备类型'" prop="deviceType">
                  <el-select size="mini" v-model="historyAlarmQuery.deviceType" @input="$set(historyAlarmQuery, 'deviceModel', undefined)">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in deviceTypeList" :key="index" :label="item.type" :value="item.type">{{ item.type }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备型号'" prop="deviceModel">
                  <el-select size="mini" v-model="historyAlarmQuery.deviceModel">
                    <el-option label="全部" :value="undefined">全部</el-option>
                    <el-option v-for="(item, index) in (deviceTypeList.find(v => v.type === historyAlarmQuery.deviceType) || {models: []}).models" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备名称'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <br/>
                
                <el-form-item :label="'告警发生时间'" prop="startBeginTime">
                  <el-date-picker
                    v-model="historyAlarmQuery.startBeginTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop="startEndTime">
                  <el-date-picker
                    v-model="historyAlarmQuery.startEndTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-form-item :label="'告警清除时间'" prop="clearBeginTime">
                  <el-date-picker
                    v-model="historyAlarmQuery.clearBeginTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop="clearEndTime">
                  <el-date-picker
                    v-model="historyAlarmQuery.clearEndTime"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-button type="primary" size="mini" @click="getHistoryAlarmList()" style="margin-left: 33px;">确认</el-button>
                <el-button type="primary" size="mini" @click="historyAlarmQuery = {}">清除</el-button>
              </el-form>
            </div>

            <div class="list df dfv">
              <div class="list-header">
                <h3>历史告警列表</h3>
                <div class="btn-group">
                  <el-button type='primary' size="mini" @click="additionalInfoToModal()">告警拓展信息</el-button>
                  <custom-column v-model="columnList" 
                    :name="'alarmHistoryListCustomColumn'" 
                    :enterprise-code="curEnterprise.code" 
                    :server-code="curServer.code"
                    :refreshCallback="getAdditionalList">自定义列</custom-column>
                </div>
              </div>
              <div class="table-box df dfv">
                <custom-table ref="historyAlarmList" :data="historyAlarmList" :columns="columnListForTable" height="100%" :ignoreList="['$selection', '$op']"></custom-table>

                <!-- Pagination -->
                <el-col :span="24" class="toolbar">
                  <el-pagination
                    layout="total, prev, pager, next,sizes, jumper"
                    @current-change="historyPagi.handleCurrentChange"
                    @size-change="historyPagi.pageSizeChange"
                    :page-sizes="[10, 15, 50, 100]"
                    :page-size="historyPagi.pageSize"
                    :total="historyPagi.total"
                    style="float:right;">
                  </el-pagination>
                </el-col>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>

    </div>  
    <el-dialog
      title="扩展信息添加"
      class="m400"
      :close-on-click-modal = "false"
      @close="additionalInfoVisible = false"
      :visible="additionalInfoVisible">
      <el-form :model="additionalInfoForm" :rules="additionalInfoRules" label-width="90px" ref="additionalInfo">
        <el-form-item label="企业名称" prop="enterpriseCode">
          <el-select size="mini" v-model="additionalInfoForm.enterpriseCode" style="width:250px">
            <el-option v-for="(item, index) in enterPriseList" :key="index" :label="item.title" :value="item.enterpriseCode">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务名称" prop="serverCode">
          <el-select size="mini" v-model="additionalInfoForm.serverCode" style="width:250px">
            <el-option v-for="(item, index) in getServerList(additionalInfoForm.enterpriseCode)" :key="index" :label="item.title" :value="item.code">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="英文名称" prop="str">
          <el-input v-model="additionalInfoForm.str" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="中文名称" prop="name">
          <el-input v-model="additionalInfoForm.name" size="mini" style="width:250px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="additionalInfoVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="additionalInfoSubmit();">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="告警确认"
      class="m400"      
      :close-on-click-modal = "false"
      @close="alarmConfirmVisible = false"
      :visible="alarmConfirmVisible">
      <el-form :model="alarmConfirmForm" :rules="alarmConfirmRules" label-width="90px" ref="alarmConfirm">
        <el-form-item label="用户姓名" prop="machineType">
          <el-input v-model="alarmConfirmForm.machineType" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="操作时间" prop="aaa">
          <el-input v-model="alarmConfirmForm.aaa" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="确认内容" prop="bbb">
          <el-input v-model="alarmConfirmForm.aaa" :rows="3" resize="none" type="textarea" style="width:250px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="alarmConfirmVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="alarmConfirmVisible = false;">确 定</el-button>
      </span>
    </el-dialog>

  </section>
</template>

<script>
import { customTable, resolveScopedSlots } from "./customColumn.vue";
import customColumn from './customColumn.vue'
export default {
  name: 'alarm-list',
  components: {
      customColumn,
      customTable,
  },
  data () {
    let scope = this;
    return {
      curEnterprise: {code: 'meitainuo'},
      curServer: {code: 'zhyd'},
      enterPriseList: [],
      nodes: [{
        name: '全部',
        children: [
          {
            name: 'diyige'
          }
        ]
      }],      
      treeSetting: {
        check: {
          enable: true,
          chkStyle: "checkbox",
          chkboxType: {
            Y: "ps",
            N: "ps"
          }
        },
        view: {
          showIcon: false,
          showLine: false,
          showTitle: false,
          dblClickExpand: false,
          fontCss: function(treeId, node) {
            return node.fsuState === false
              ? { color: "#9A9A9A" }
              : { color: "#fff" };
          }
        },
        data: {
          key: {
            children: "children",
            name: "name",
            title: "code"
          }
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            if (treeNode.nocheck === false) {
              //如果这个节点是可选的，单击文字的时候将选中此项
              let treeObj = scope.$refs.tree.ztreeObj
              treeObj.checkNode(treeNode, !treeNode.checked, true, true);
            }
          },
          beforeCheck: (treeId, treeNode) => {
            if (this.curDistrict === 0) { return true; }
            let invalid = true;
            const traversal = node => {
              let parent = node.getParentNode()
              if (node.level >= this.curDistrict) {
                if (parent.children.filter(v => v.checked && (v.check_Child_State === 2 || v.check_Child_State === -1)).length != parent.children.length - 1) { invalid = false; return; }
                traversal(parent)
              }
              return true
            }
            if (
              !treeNode.checked &&
              treeNode.level >= this.curDistrict && 
              traversal(treeNode) && invalid
            ) {
              return false;
            }
            return true;
          }
          // beforeExpand: beforeExpand
        }
      },
      curView: '1',
      activeAlarmQuery: {},
      activeAlarmList: [{}, {}],
      activePagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.activePagi.page = v;
          this.getActiveAlarmList();
        },
        pageSizeChange: () => {
          this.activePagi.pageSize = v;
          this.getActiveAlarmList();
        },
      },
      historyAlarmQuery: {},
      historyAlarmList: [{}, {}],
      historyPagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.historyPagi.page = v;
          this.getHistoryAlarmList();
        },
        pageSizeChange: () => {
          this.historyPagi.pageSize = v;
          this.getHistoryAlarmList();
        },
      },
      defaultColumnList: [
        {id: 'index', label: '序号', type: 'index', value: true},
        {id: 'name', label: '告警名称', prop: 'name', value: true},
        {id: 'value', label: '告警值', prop: 'value', value: true},
        {id: 'level', label: '告警等级', prop: 'level', value: true},
        {id: 'deviceId', label: '设备ID', prop: 'deviceId', value: true},
        {id: 'treport', label: '告警发生时间', prop: 'treport', value: true},
        {id: 'state', label: '告警状态', prop: 'state', value: true},
        {id: 'deviceInfos', label: '设备信息', prop: 'deviceInfos', value: true},
        {id: 'xx8', label: '告警确认状态', prop: '设备名称', value: true},
        {id: 'xx9', label: '操作', prop: '$op', value: true, render: h => {
          return h(                    
            'el-table-column',
            {
              props: {
                label: '操作',
              },
              scopedSlots: resolveScopedSlots([{
                key: "default",
                fn: scope => {
                  return [
                    h('a', {
                      style: 'margin-right: 5px;',
                      on: {
                        click: () => {this.alarmConfirmToModal(scope.row)}
                      }
                    }, '告警确认'),
                    h('a', {
                      on: {
                        click () {this.deleteAlarmToModal(scope.row);}
                      }
                    }, '清除'),
                  ];
                }
              }])
            }
          )
        }},
      ],
      columnList: [],
      additionalColumnList: [],
      additionalInfoVisible: false,
      additionalInfoForm: {},
      alarmConfirmVisible: false,
      alarmConfirmForm: {},
      alarmLevelList: [],
      deviceTypeList: [],
    }
  },
  computed: {
    additionalInfoRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    alarmConfirmRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    columnListForTable () {
      return [{type: 'selection', value: 'true', prop: '$selection'}].concat(this.columnList);
    },
  },
  methods: {
    getUniqueServiceList () {
      this.$api.enterpriseLevelControllerGetUniqueServiceList().then(res => {
        this.enterPriseList = res;
      })
    },
    getServerList (enterpriseCode) {
      return (this.enterPriseList.find(v => v.enterpriseCode === enterpriseCode) || {serverCodes: []}).serverCodes
    },
    getAlarmLevelList () {
      this.$api.enterpriseLevelControllerGetLastUse({
        enterpriseCode: this.curEnterprise.code,
        serverCode: this.curServer.code,
      }).then(res => {
        this.alarmLevelList = res;
      })
    },
    getDeviceTypeList () {
      this.$api.deviceTypeLevelControllerGetDeviceTypeList().then(res => {
        let obj = {}
        let list = []
        res.deviceTypeResultList.forEach(v => {
          if (!obj[v.deviceType]) { obj[v.deviceType] = {type: v.deviceType, models: []} }
          obj[v.deviceType].models.push(v.model);
        })
        for (let i in obj) {
          list.push(obj[i]);
        }

        this.deviceTypeList = list
      })
    },
    getActiveAlarmList () {
      let query = Object.assign({}, this.activeAlarmQuery);

      query.startBeginTime && (query.startBeginTime = query.startBeginTime.getTime());
      query.startEndTime && (query.startEndTime = query.startEndTime.getTime());

      this.$api.alarmControllerList(Object.assign({
        pageSize: this.activePagi.pageSize,
        currentPage: this.activePagi.page,
        type: '实时告警',
        enterpriseCode: this.curEnterprise.code,
        serverCode: this.curServer.code,
      }, query)).then(res => {
        this.activeAlarmList = res.list;
        this.activePagi.total = res.count;
      })
    },
    getHistoryAlarmList () {
      let query = Object.assign({}, this.historyAlarmQuery);

      query.startBeginTime && (query.startBeginTime = query.startBeginTime.getTime());
      query.startEndTime && (query.startEndTime = query.startEndTime.getTime());
      query.clearBeginTime && (query.clearBeginTime = query.clearBeginTime.getTime());
      query.clearEndTime && (query.clearEndTime = query.clearEndTime.getTime());

      this.$api.alarmControllerList(Object.assign({
        pageSize: this.historyPagi.pageSize,
        currentPage: this.historyPagi.page,
        type: '历史告警',
        enterpriseCode: this.curEnterprise.code,
        serverCode: this.curServer.code,
      }, query)).then(res => {
        this.historyAlarmList = res.list;
        this.historyPagi.total = res.count;
      })
    },
    additionalInfoToModal () {
      this.additionalInfoVisible = true;
      this.$nextTick(() => {
        this.additionalInfoForm = {};
      })
    },
    additionalInfoSubmit () {
      let query = {
        serverCode: this.additionalInfoForm.serverCode,
        enterpriseCode: this.additionalInfoForm.enterpriseCode,
        proStr: this.additionalInfoForm.str,
        proName: this.additionalInfoForm.name,
      }
      this.$api.alarmauxilaryControllerAdd(query).then(res => {
        this.getAdditionalList();
        this.additionalInfoVisible = false;
      })
    },
    alarmConfirmToModal (alarm) {
      let list;
      if (!alarm) {
        list = this.$refs.activeAlarmList.$refs.table.selection;
        if (list.length < 1) {
          this.$message({
            type: 'warning',
            message: '请至少选择一条告警'
          })
          return;
        }
      }
      this.alarmConfirmAlarmList = list || [alarm];
      this.alarmConfirmVisible = true;
    },
    alarmConfirmSubmit () {},
    getAdditionalList () {
      this.$api.alarmauxilaryControllerGet({
        enterpriseCode: this.curEnterprise.code,
        serverCode: this.curServer.code
      }).then(res => {
        !res.proStrList && (res.proStrList = []);
        !res.proStrList && (res.proStrList = []);
        this.additionalColumnList = []
        res.proStrList.forEach((v, i) => {
          this.additionalColumnList.push({
            id: v,
            prop: v,
            label: res.proNameList[i],
            value: true,
            additional: true
          })
        })
        this.columnList = [].concat(this.defaultColumnList).concat(this.additionalColumnList)
      })
    },
    deleteAlarmToModal (alarm) {
      this.$confirm(`确定要删除选中的数据吗？<br/>注意：该内容删除之后无法恢复！`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        
      }).catch(() => {

      })
    },
  },
  mounted () {
    this.getUniqueServiceList();
    this.getAdditionalList();
    this.getActiveAlarmList();
    this.getHistoryAlarmList();
    this.getAlarmLevelList();
    this.getDeviceTypeList();
  }
}
</script>

<style lang="less" scoped>
#alarm-list-page{
  background-color: #011838;
  .left-tree{
    width: 210px;
    margin: 10px;
    margin-right: 0;
    padding: 10px;
    height: calc(100% - 40px);
    background-color: #0B1D3B;
  }
  .right-content{
    margin: 10px;
    flex: 1;
    height: calc(100% - 20px);
    background-color: #0B1D3B;
    &>.el-tabs{
      height: 100%;
    }
  }
  .query-form{
    min-height: 50px;
    width: 100%;
    margin-bottom: 16px;
  }
  .list{
    flex: 1;
    overflow: hidden;
    padding-bottom: 10px;
    .table-box{
      flex: 1;
      overflow: hidden;
    }    
  }
}
</style>


